import React, { useState, useEffect } from 'react';
import { useMutation } from '@tanstack/react-query';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Switch } from '@/components/ui/switch';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Badge } from '@/components/ui/badge';
import { toast } from 'sonner';
import {
  createDefaultAssistantTemplate,
  updateDefaultAssistantTemplate,
  type DefaultAssistantTemplate
} from '@/api/services/defaultAssistantTemplates/defaultAssistantTemplateService';

interface DefaultAssistantTemplateFormProps {
  template?: DefaultAssistantTemplate | null;
  isEditing?: boolean;
  onSuccess: () => void;
  onCancel: () => void;
}

const CATEGORIES = [
  'General',
  'Business',
  'Sales',
  'Support',
  'Scheduling',
  'Technical',
  'Healthcare',
  'Education',
  'Finance',
  'Real Estate'
];

const VOICE_PROVIDERS = ['vapi', '11labs', 'openai', 'azure', 'deepgram', 'cartesia', 'playht', 'lmnt', 'neets', 'rime-ai'];
const MODEL_PROVIDERS = ['openai', 'anthropic', 'groq', 'together', 'anyscale', 'perplexity'];
const TRANSCRIBER_PROVIDERS = ['deepgram', 'openai', 'azure', 'google'];

export const DefaultAssistantTemplateForm: React.FC<DefaultAssistantTemplateFormProps> = ({
  template,
  isEditing = false,
  onSuccess,
  onCancel
}) => {
  const [formData, setFormData] = useState<Partial<DefaultAssistantTemplate>>({
    name: '',
    description: '',
    category: 'General',
    first_message: '',
    system_message: 'You are a helpful AI assistant.',
    model: {
      provider: 'openai',
      model: 'gpt-4',
      temperature: 0.7,
      maxTokens: 1000,
      messages: [
        { role: 'system', content: 'You are a helpful AI assistant.' }
      ]
    },
    voice: {
      provider: 'vapi',
      voiceId: 'Cole',
      speed: 1.0
    },
    transcriber: {
      provider: 'deepgram',
      language: 'en',
      confidenceThreshold: 0.4
    },
    background_sound: 'off',
    max_duration_seconds: 1800,
    is_active: true,
    is_featured: false,
    sort_order: 0,
    ...template
  });

  const createMutation = useMutation({
    mutationFn: createDefaultAssistantTemplate,
    onSuccess: () => {
      toast.success('Template created successfully');
      onSuccess();
    },
    onError: (error: any) => {
      console.error('Error creating template:', error);
    }
  });

  const updateMutation = useMutation({
    mutationFn: ({ templateId, data }: { templateId: string; data: Partial<DefaultAssistantTemplate> }) =>
      updateDefaultAssistantTemplate(templateId, data),
    onSuccess: () => {
      toast.success('Template updated successfully');
      onSuccess();
    },
    onError: (error: any) => {
      console.error('Error updating template:', error);
    }
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.name || !formData.description) {
      toast.error('Name and description are required');
      return;
    }

    // Update system message in model.messages
    const updatedFormData = {
      ...formData,
      model: {
        ...formData.model,
        messages: [
          { role: 'system', content: formData.system_message || 'You are a helpful AI assistant.' }
        ]
      }
    };

    if (isEditing && template?.template_id) {
      updateMutation.mutate({
        templateId: template.template_id,
        data: updatedFormData
      });
    } else {
      createMutation.mutate(updatedFormData);
    }
  };

  const handleFieldChange = (field: string, value: any) => {
    if (field.includes('.')) {
      const [parent, child] = field.split('.');
      setFormData(prev => ({
        ...prev,
        [parent]: {
          ...prev[parent as keyof typeof prev],
          [child]: value
        }
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        [field]: value
      }));
    }
  };

  const isLoading = createMutation.isPending || updateMutation.isPending;

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <Tabs defaultValue="basic" className="w-full">
        <TabsList className="grid w-full grid-cols-4 bg-white/5 border border-white/10">
          <TabsTrigger value="basic" className="text-gray-300 data-[state=active]:bg-indigo-600 data-[state=active]:text-white">Basic Info</TabsTrigger>
          <TabsTrigger value="ai-config" className="text-gray-300 data-[state=active]:bg-indigo-600 data-[state=active]:text-white">AI Configuration</TabsTrigger>
          <TabsTrigger value="advanced" className="text-gray-300 data-[state=active]:bg-indigo-600 data-[state=active]:text-white">Advanced</TabsTrigger>
          <TabsTrigger value="settings" className="text-gray-300 data-[state=active]:bg-indigo-600 data-[state=active]:text-white">Settings</TabsTrigger>
        </TabsList>

        <TabsContent value="basic" className="space-y-4">
          <Card className="bg-white/5 backdrop-blur-md border border-white/10">
            <CardHeader>
              <CardTitle className="text-white">Basic Information</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="name" className="text-gray-300">Template Name *</Label>
                  <Input
                    id="name"
                    value={formData.name || ''}
                    onChange={(e) => handleFieldChange('name', e.target.value)}
                    placeholder="Customer Service Assistant"
                    required
                    className="bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                  />
                </div>
                <div>
                  <Label htmlFor="category" className="text-gray-300">Category</Label>
                  <Select value={formData.category} onValueChange={(value) => handleFieldChange('category', value)}>
                    <SelectTrigger className="bg-white/5 border-white/20 text-white">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-[#0e1340] border-white/10">
                      {CATEGORIES.map(category => (
                        <SelectItem key={category} value={category} className="text-white hover:bg-white/10 focus:bg-white/10">
                          {category}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div>
                <Label htmlFor="description" className="text-gray-300">Description *</Label>
                <Textarea
                  id="description"
                  value={formData.description || ''}
                  onChange={(e) => handleFieldChange('description', e.target.value)}
                  placeholder="Describe what this assistant template is designed for..."
                  rows={3}
                  required
                  className="bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                />
              </div>

              <div>
                <Label htmlFor="first_message" className="text-gray-300">First Message</Label>
                <Textarea
                  id="first_message"
                  value={formData.first_message || ''}
                  onChange={(e) => handleFieldChange('first_message', e.target.value)}
                  placeholder="Hello! How can I help you today?"
                  rows={2}
                  className="bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                />
              </div>

              <div>
                <Label htmlFor="system_message" className="text-gray-300">System Message</Label>
                <Textarea
                  id="system_message"
                  value={formData.system_message || ''}
                  onChange={(e) => handleFieldChange('system_message', e.target.value)}
                  placeholder="You are a helpful AI assistant..."
                  rows={4}
                  className="bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                />
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="ai-config" className="space-y-4">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
            {/* Model Configuration */}
            <Card className="bg-white/5 backdrop-blur-md border border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Model</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label className="text-gray-300">Provider</Label>
                  <Select
                    value={formData.model?.provider}
                    onValueChange={(value) => handleFieldChange('model.provider', value)}
                  >
                    <SelectTrigger className="bg-white/5 border-white/20 text-white">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-[#0e1340] border-white/10">
                      {MODEL_PROVIDERS.map(provider => (
                        <SelectItem key={provider} value={provider} className="text-white hover:bg-white/10 focus:bg-white/10">
                          {provider}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label className="text-gray-300">Model</Label>
                  <Input
                    value={formData.model?.model || ''}
                    onChange={(e) => handleFieldChange('model.model', e.target.value)}
                    placeholder="gpt-4"
                    className="bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                  />
                </div>
                <div>
                  <Label className="text-gray-300">Temperature</Label>
                  <Input
                    type="number"
                    min="0"
                    max="2"
                    step="0.1"
                    value={formData.model?.temperature || 0.7}
                    onChange={(e) => handleFieldChange('model.temperature', parseFloat(e.target.value))}
                    className="bg-white/5 border-white/20 text-white focus:border-indigo-500"
                  />
                </div>
              </CardContent>
            </Card>

            {/* Voice Configuration */}
            <Card className="bg-white/5 backdrop-blur-md border border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Voice</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label className="text-gray-300">Provider</Label>
                  <Select
                    value={formData.voice?.provider}
                    onValueChange={(value) => handleFieldChange('voice.provider', value)}
                  >
                    <SelectTrigger className="bg-white/5 border-white/20 text-white">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-[#0e1340] border-white/10">
                      {VOICE_PROVIDERS.map(provider => (
                        <SelectItem key={provider} value={provider} className="text-white hover:bg-white/10 focus:bg-white/10">
                          {provider}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label className="text-gray-300">Voice ID</Label>
                  <Input
                    value={formData.voice?.voiceId || ''}
                    onChange={(e) => handleFieldChange('voice.voiceId', e.target.value)}
                    placeholder="Cole"
                    className="bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                  />
                </div>
                <div>
                  <Label className="text-gray-300">Speed</Label>
                  <Input
                    type="number"
                    min="0.5"
                    max="2"
                    step="0.1"
                    value={formData.voice?.speed || 1.0}
                    onChange={(e) => handleFieldChange('voice.speed', parseFloat(e.target.value))}
                    className="bg-white/5 border-white/20 text-white focus:border-indigo-500"
                  />
                </div>
              </CardContent>
            </Card>

            {/* Transcriber Configuration */}
            <Card className="bg-white/5 backdrop-blur-md border border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Transcriber</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label className="text-gray-300">Provider</Label>
                  <Select
                    value={formData.transcriber?.provider}
                    onValueChange={(value) => handleFieldChange('transcriber.provider', value)}
                  >
                    <SelectTrigger className="bg-white/5 border-white/20 text-white">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-[#0e1340] border-white/10">
                      {TRANSCRIBER_PROVIDERS.map(provider => (
                        <SelectItem key={provider} value={provider} className="text-white hover:bg-white/10 focus:bg-white/10">
                          {provider}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label className="text-gray-300">Language</Label>
                  <Input
                    value={formData.transcriber?.language || 'en'}
                    onChange={(e) => handleFieldChange('transcriber.language', e.target.value)}
                    placeholder="en"
                    className="bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                  />
                </div>
                <div>
                  <Label className="text-gray-300">Confidence Threshold</Label>
                  <Input
                    type="number"
                    min="0"
                    max="1"
                    step="0.1"
                    value={formData.transcriber?.confidenceThreshold || 0.4}
                    onChange={(e) => handleFieldChange('transcriber.confidenceThreshold', parseFloat(e.target.value))}
                    className="bg-white/5 border-white/20 text-white focus:border-indigo-500"
                  />
                </div>
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent value="advanced" className="space-y-4">
          <Card className="bg-white/5 backdrop-blur-md border border-white/10">
            <CardHeader>
              <CardTitle className="text-white">Advanced Settings</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label className="text-gray-300">Background Sound</Label>
                  <Select
                    value={formData.background_sound}
                    onValueChange={(value) => handleFieldChange('background_sound', value)}
                  >
                    <SelectTrigger className="bg-white/5 border-white/20 text-white">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-[#0e1340] border-white/10">
                      <SelectItem value="off" className="text-white hover:bg-white/10 focus:bg-white/10">Off</SelectItem>
                      <SelectItem value="office" className="text-white hover:bg-white/10 focus:bg-white/10">Office</SelectItem>
                      <SelectItem value="cafe" className="text-white hover:bg-white/10 focus:bg-white/10">Cafe</SelectItem>
                      <SelectItem value="nature" className="text-white hover:bg-white/10 focus:bg-white/10">Nature</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label className="text-gray-300">Max Duration (seconds)</Label>
                  <Input
                    type="number"
                    min="60"
                    max="7200"
                    value={formData.max_duration_seconds || 1800}
                    onChange={(e) => handleFieldChange('max_duration_seconds', parseInt(e.target.value))}
                    className="bg-white/5 border-white/20 text-white focus:border-indigo-500"
                  />
                </div>
              </div>

              <div>
                <Label htmlFor="end_call_message" className="text-gray-300">End Call Message</Label>
                <Textarea
                  id="end_call_message"
                  value={formData.end_call_message || ''}
                  onChange={(e) => handleFieldChange('end_call_message', e.target.value)}
                  placeholder="Thank you for calling. Have a great day!"
                  rows={2}
                  className="bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                />
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="settings" className="space-y-4">
          <Card className="bg-white/5 backdrop-blur-md border border-white/10">
            <CardHeader>
              <CardTitle className="text-white">Template Settings</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center justify-between">
                <div>
                  <Label className="text-gray-300">Active</Label>
                  <p className="text-sm text-gray-400">Make this template available to users</p>
                </div>
                <Switch
                  checked={formData.is_active}
                  onCheckedChange={(checked) => handleFieldChange('is_active', checked)}
                  className="data-[state=checked]:bg-indigo-600"
                />
              </div>

              <div className="flex items-center justify-between">
                <div>
                  <Label className="text-gray-300">Featured</Label>
                  <p className="text-sm text-gray-400">Show this template prominently</p>
                </div>
                <Switch
                  checked={formData.is_featured}
                  onCheckedChange={(checked) => handleFieldChange('is_featured', checked)}
                  className="data-[state=checked]:bg-indigo-600"
                />
              </div>

              <div>
                <Label className="text-gray-300">Sort Order</Label>
                <Input
                  type="number"
                  min="0"
                  value={formData.sort_order || 0}
                  onChange={(e) => handleFieldChange('sort_order', parseInt(e.target.value))}
                  className="bg-white/5 border-white/20 text-white focus:border-indigo-500"
                />
                <p className="text-sm text-gray-400 mt-1">Lower numbers appear first</p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      <div className="flex justify-end gap-3 pt-4 border-t border-white/10">
        <Button type="button" variant="outline" onClick={onCancel} className="bg-transparent border-white/20 text-white hover:bg-white/10 hover:text-white">
          Cancel
        </Button>
        <Button type="submit" disabled={isLoading} className="bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white border-0">
          {isLoading ? 'Saving...' : isEditing ? 'Update Template' : 'Create Template'}
        </Button>
      </div>
    </form>
  );
};
