import React from 'react';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { 
  Bot, 
  MessageSquare, 
  Mic, 
  Brain, 
  Clock, 
  Star,
  Users,
  Settings
} from 'lucide-react';
import { type DefaultAssistantTemplate } from '@/api/services/defaultAssistantTemplates/defaultAssistantTemplateService';

interface TemplatePreviewDialogProps {
  template: DefaultAssistantTemplate | null;
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

export const TemplatePreviewDialog: React.FC<TemplatePreviewDialogProps> = ({
  template,
  open,
  onOpenChange
}) => {
  if (!template) return null;

  const getCategoryColor = (category: string) => {
    const colors: Record<string, string> = {
      'Business': 'bg-blue-500/20 text-blue-300 border border-blue-500/30',
      'Sales': 'bg-green-500/20 text-green-300 border border-green-500/30',
      'Support': 'bg-purple-500/20 text-purple-300 border border-purple-500/30',
      'Scheduling': 'bg-orange-500/20 text-orange-300 border border-orange-500/30',
      'General': 'bg-gray-500/20 text-gray-300 border border-gray-500/30'
    };
    return colors[category] || 'bg-gray-500/20 text-gray-300 border border-gray-500/30';
  };

  const formatDuration = (seconds: number) => {
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    if (hours > 0) {
      return `${hours}h ${minutes % 60}m`;
    }
    return `${minutes}m`;
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-7xl neon-fire-scroll rounded-lg max-h-[90vh] overflow-y-auto bg-[#0e1340] border border-white/10 text-white" aria-describedby="template-preview-description">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-white">
            <Bot className="h-5 w-5 text-indigo-400" />
            Template Name: {template.name}
          </DialogTitle>
          <DialogDescription id="template-preview-description" className="text-gray-400">
            Preview the details and configuration of this assistant template. You can view all settings, prompts, and capabilities.
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-6">
          {/* Header Info */}
          <div className="flex items-start justify-between">
            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <Badge className={getCategoryColor(template.category)}>
                 Category:   {template.category}
                </Badge>
                {/* {template.is_featured && (
                  <Badge className="bg-yellow-500/20 text-yellow-300 border border-yellow-500/30">
                    <Star className="h-3 w-3 mr-1 fill-current" />
                    Featured
                  </Badge>
                )} */}
                {!template.is_active && (
                  <Badge variant="secondary" className="bg-gray-500/20 text-gray-300 border border-gray-500/30">Inactive</Badge>
                )}
              </div>
              <p className="text-gray-300">{template.description}</p>
            </div>
            {/* <div className="text-right text-sm text-gray-400">
              <div className="flex items-center gap-1">
                <Users className="h-4 w-4" />
                {template.usage_count || 0} uses
              </div>
            </div> */}
          </div>

          <Separator className="bg-white/10" />

          {/* Configuration Overview */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {/* AI Model */}
            <Card className="bg-white/5 backdrop-blur-md border border-white/10">
              <CardHeader className="pb-3">
                <CardTitle className="text-sm flex items-center gap-2 text-white">
                  <Brain className="h-4 w-4 text-indigo-400" />
                  AI Model
                </CardTitle>
              </CardHeader>
              <CardContent className="pt-0 space-y-2">
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Provider:</span> {template.model?.provider || 'N/A'}
                </div>
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Model:</span> {template.model?.model || 'N/A'}
                </div>
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Temperature:</span> {template.model?.temperature || 'N/A'}
                </div>
              </CardContent>
            </Card>

            {/* Voice */}
            <Card className="bg-white/5 backdrop-blur-md border border-white/10">
              <CardHeader className="pb-3">
                <CardTitle className="text-sm flex items-center gap-2 text-white">
                  <Mic className="h-4 w-4 text-indigo-400" />
                  Voice
                </CardTitle>
              </CardHeader>
              <CardContent className="pt-0 space-y-2">
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Provider:</span> {template.voice?.provider || 'N/A'}
                </div>
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Voice ID:</span> {template.voice?.voiceId || 'N/A'}
                </div>
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Speed:</span> {template.voice?.speed || 'N/A'}
                </div>
              </CardContent>
            </Card>

            {/* Settings */}
            <Card className="bg-white/5 backdrop-blur-md border border-white/10">
              <CardHeader className="pb-3">
                <CardTitle className="text-sm flex items-center gap-2 text-white">
                  <Settings className="h-4 w-4 text-indigo-400" />
                  Settings
                </CardTitle>
              </CardHeader>
              <CardContent className="pt-0 space-y-2">
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Max Duration:</span> {
                    template.max_duration_seconds
                      ? formatDuration(template.max_duration_seconds)
                      : 'N/A'
                  }
                </div>
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Background:</span> {template.background_sound || 'Off'}
                </div>
                <div className="text-sm text-gray-300">
                  <span className="font-medium text-white">Transcriber:</span> {template.transcriber?.provider || 'N/A'}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Messages */}
          <div className="space-y-4">
            {template.first_message && (
              <Card className="bg-white/5 backdrop-blur-md border border-white/10">
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm flex items-center gap-2 text-white">
                    <MessageSquare className="h-4 w-4 text-indigo-400" />
                    First Message
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-0">
                  <div className="bg-indigo-500/10 p-3 rounded-lg border border-indigo-500/30">
                    <p className="text-sm text-indigo-200">{template.first_message}</p>
                  </div>
                </CardContent>
              </Card>
            )}

            {template.system_message && (
              <Card className="bg-white/5 backdrop-blur-md border border-white/10">
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm flex items-center gap-2 text-white">
                    <Bot className="h-4 w-4 text-indigo-400" />
                    System Message
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-0">
                  <div className="bg-white/5 p-3 rounded-lg border border-white/10">
                    <p className="text-sm text-gray-300 whitespace-pre-wrap">{template.system_message}</p>
                  </div>
                </CardContent>
              </Card>
            )}

            {template.end_call_message && (
              <Card className="bg-white/5 backdrop-blur-md border border-white/10">
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm flex items-center gap-2 text-white">
                    <MessageSquare className="h-4 w-4 text-indigo-400" />
                    End Call Message
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-0">
                  <div className="bg-green-500/10 p-3 rounded-lg border border-green-500/30">
                    <p className="text-sm text-green-300">{template.end_call_message}</p>
                  </div>
                </CardContent>
              </Card>
            )}
          </div>

          {/* Advanced Configuration */}
          {(template.functions && template.functions.length > 0) && (
            <Card className="bg-white/5 backdrop-blur-md border border-white/10">
              <CardHeader className="pb-3">
                <CardTitle className="text-sm text-white">Functions</CardTitle>
              </CardHeader>
              <CardContent className="pt-0">
                <div className="text-sm text-gray-300">
                  {template.functions.length} function(s) configured
                </div>
              </CardContent>
            </Card>
          )}

          {/* Metadata */}
          <div className="text-xs text-gray-400 pt-4 border-t border-white/10">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <span className="font-medium text-gray-300">Created:</span> {
                  template.created_at
                    ? new Date(template.created_at).toLocaleDateString()
                    : 'N/A'
                }
              </div>
              <div>
                <span className="font-medium text-gray-300">Last Updated:</span> {
                  template.updated_at
                    ? new Date(template.updated_at).toLocaleDateString()
                    : 'N/A'
                }
              </div>
            </div>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};
