import React, { useState, useEffect } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { 
  Plus, 
  Edit, 
  Trash2, 
  Bot, 
  Star, 
  Users, 
  Filter,
  Search,
  Eye,
  Copy
} from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { toast } from 'sonner';
import {
  getAllDefaultAssistantTemplates,
  getTemplateCategories,
  deleteDefaultAssistantTemplate,
  type DefaultAssistantTemplate,
  type TemplateCategory,
  type TemplateFilters
} from '@/api/services/defaultAssistantTemplates/defaultAssistantTemplateService';
import { DefaultAssistantTemplateForm } from './components/DefaultAssistantTemplateForm';
import { TemplatePreviewDialog } from './components/TemplatePreviewDialog';

const DefaultAssistants: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false);
  const [isPreviewDialogOpen, setIsPreviewDialogOpen] = useState(false);
  const [selectedTemplate, setSelectedTemplate] = useState<DefaultAssistantTemplate | null>(null);

  const queryClient = useQueryClient();

  // Fetch templates
  const { data: templatesData, isLoading: templatesLoading } = useQuery({
    queryKey: ['defaultAssistantTemplates', { category: selectedCategory !== 'all' ? selectedCategory : undefined }],
    queryFn: () => {
      const filters: TemplateFilters = {};
      if (selectedCategory !== 'all') {
        filters.category = selectedCategory;
      }
      return getAllDefaultAssistantTemplates(filters);
    }
  });

  // Fetch categories
  const { data: categoriesData } = useQuery({
    queryKey: ['templateCategories'],
    queryFn: getTemplateCategories
  });

  // Delete mutation
  const deleteMutation = useMutation({
    mutationFn: deleteDefaultAssistantTemplate,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['defaultAssistantTemplates'] });
      queryClient.invalidateQueries({ queryKey: ['templateCategories'] });
    }
  });

  const templates = templatesData?.data || [];
  const categories = categoriesData?.data || [];

  // Filter templates based on search term
  const filteredTemplates = templates.filter((template: DefaultAssistantTemplate) =>
    template.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    template.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
    template.category.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleEdit = (template: DefaultAssistantTemplate) => {
    setSelectedTemplate(template);
    setIsEditDialogOpen(true);
  };

  const handlePreview = (template: DefaultAssistantTemplate) => {
    setSelectedTemplate(template);
    setIsPreviewDialogOpen(true);
  };

  const handleDelete = async (template: DefaultAssistantTemplate) => {
    if (window.confirm(`Are you sure you want to delete "${template.name}"? This action cannot be undone.`)) {
      deleteMutation.mutate(template.template_id);
    }
  };

  const handleDuplicate = (template: DefaultAssistantTemplate) => {
    const { template_id, id, created_at, updated_at, ...templateWithoutIds } = template;
    const duplicatedTemplate: DefaultAssistantTemplate = {
      ...templateWithoutIds,
      name: `${template.name} (Copy)`,
      template_id: '', // Will be generated by backend
      usage_count: 0
    };
    setSelectedTemplate(duplicatedTemplate);
    setIsCreateDialogOpen(true);
  };

  const getCategoryColor = (category: string) => {
    const colors: Record<string, string> = {
      'Business': 'bg-blue-500/20 text-blue-300 border border-blue-500/30',
      'Sales': 'bg-green-500/20 text-green-300 border border-green-500/30',
      'Support': 'bg-purple-500/20 text-purple-300 border border-purple-500/30',
      'Scheduling': 'bg-orange-500/20 text-orange-300 border border-orange-500/30',
      'General': 'bg-gray-500/20 text-gray-300 border border-gray-500/30'
    };
    return colors[category] || 'bg-gray-500/20 text-gray-300 border border-gray-500/30';
  };

  return (
    <div className="container mx-auto p-6 space-y-6 min-w-full min-h-screen app-shell-bg relative overflow-hidden">
      {/* Neon glow layer */}
      <div
        aria-hidden
        className="absolute inset-0 pointer-events-none bg-gradient-to-r from-indigo-500/10 via-purple-500/10 to-pink-500/10"
      />

      {/* Header */}
      <div className="relative flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-white">Default Assistant Templates</h1>
          <p className="text-gray-300 mt-1">
            Manage default assistant templates that users can choose from when creating assistants
          </p>
        </div>
        <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
          <DialogTrigger asChild>
            <button className="px-5 py-2
    rounded-lg
    text-sm font-semibold
    text-white
    bg-[linear-gradient(180deg,rgba(90,110,255,0.45),rgba(36,46,151,0.35))]
    border border-white/15
    shadow-[0_2px_5px_rgba(90,110,255,0.45)]
    hover:shadow-[0_5px_10px_rgba(90,110,255,0.65)]
    hover:bg-[linear-gradient(180deg,rgba(90,110,255,0.6),rgba(36,46,151,0.45))]
    disabled:opacity-40
    disabled:cursor-not-allowed
    transition-all
    flex items-center gap-2">
              <Plus className="h-4 w-4 mr-2" />
              Create Template
            </button>
          </DialogTrigger>
          <DialogContent className="max-w-6xl max-h-[90vh] overflow-y-auto bg-[#0e1340] border border-white/10 text-white">
            <DialogHeader>
              <DialogTitle className="flex items-center gap-2 text-white">
                <Bot className="h-5 w-5 text-indigo-400" />
                Create Default Assistant Template
              </DialogTitle>
            </DialogHeader>
            <DefaultAssistantTemplateForm
              template={selectedTemplate}
              onSuccess={() => {
                setIsCreateDialogOpen(false);
                setSelectedTemplate(null);
                queryClient.invalidateQueries({ queryKey: ['defaultAssistantTemplates'] });
                queryClient.invalidateQueries({ queryKey: ['templateCategories'] });
              }}
              onCancel={() => {
                setIsCreateDialogOpen(false);
                setSelectedTemplate(null);
              }}
            />
          </DialogContent>
        </Dialog>
      </div>

      {/* Filters */}
      <Card className="relative bg-white/5 backdrop-blur-md border border-white/10">
        <CardContent className="p-4">
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="flex-1">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
                <Input
                  placeholder="Search templates..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10 bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-indigo-500"
                />
              </div>
            </div>
            <div className="sm:w-48">
              <Select value={selectedCategory} onValueChange={setSelectedCategory}>
                <SelectTrigger className="bg-white/5 border-white/20 text-white">
                  <Filter className="h-4 w-4 mr-2 text-gray-400" />
                  <SelectValue placeholder="Category" />
                </SelectTrigger>
                <SelectContent className="bg-[#0e1340] border-white/10 text-white">
                  <SelectItem value="all" className="text-white hover:bg-white/10 focus:bg-white/10">All Categories</SelectItem>
                  {categories.map((category: TemplateCategory) => (
                    <SelectItem key={category.category} value={category.category} className="text-white hover:bg-white/10 focus:bg-white/10">
                      {category.category} ({category.count})
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Templates Grid */}
      {templatesLoading ? (
        <div className="relative grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {[...Array(6)].map((_, i) => (
            <Card key={i} className="animate-pulse bg-white/5 border border-white/10">
              <CardHeader>
                <div className="h-4 bg-white/10 rounded w-3/4"></div>
                <div className="h-3 bg-white/10 rounded w-1/2"></div>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="h-3 bg-white/10 rounded"></div>
                  <div className="h-3 bg-white/10 rounded w-5/6"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : filteredTemplates.length === 0 ? (
        <Card className="relative bg-white/5 backdrop-blur-md border border-white/10">
          <CardContent className="p-12 text-center">
            <Bot className="h-12 w-12 text-indigo-400 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-white mb-2">No templates found</h3>
            <p className="text-gray-300 mb-4">
              {searchTerm || selectedCategory !== 'all'
                ? 'Try adjusting your search or filter criteria.'
                : 'Get started by creating your first default assistant template.'
              }
            </p>
            {!searchTerm && selectedCategory === 'all' && (
              <Button onClick={() => setIsCreateDialogOpen(true)} className="bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white">
                <Plus className="h-4 w-4 mr-2" />
                Create Template
              </Button>
            )}
          </CardContent>
        </Card>
      ) : (
        <div className="relative grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredTemplates.map((template: DefaultAssistantTemplate) => (
            <Card key={template.template_id} className="bg-white/5 backdrop-blur-md border border-white/10 hover:border-indigo-500/50 hover:shadow-lg hover:shadow-indigo-500/10 transition-all">
              <CardHeader className="pb-3">
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <CardTitle className="text-lg flex items-center gap-2 text-white">
                      {/* {template.is_featured && <Star className="h-4 w-4 text-yellow-500 fill-current" />} */}
                      {template.name}
                    </CardTitle>
                    <div className="flex items-center gap-2 mt-2">
                      <Badge className={getCategoryColor(template.category)}>
                        {template.category}
                      </Badge>
                      {!template.is_active && (
                        <Badge variant="secondary" className="bg-gray-500/20 text-gray-300 border border-gray-500/30">Inactive</Badge>
                      )}
                    </div>
                  </div>
                </div>
              </CardHeader>
              <CardContent className="pt-0">
                <p className="text-gray-300 text-sm mb-4 line-clamp-3">
                  {template.description}
                </p>
                <div className="flex items-center justify-between text-sm text-gray-400 mb-4">
                  <div className="flex items-center gap-1">
                    <Users className="h-4 w-4" />
                    {template.usage_count || 0} uses
                  </div>
                </div>
                <div className="flex gap-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handlePreview(template)}
                    className="flex-1 bg-transparent border-white/20 text-white hover:bg-white/10 hover:text-white"
                  >
                    <Eye className="h-4 w-4 mr-1" />
                    Preview
                  </Button>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleEdit(template)}
                    className="bg-transparent border-white/20 text-white hover:bg-white/10 hover:text-white"
                  >
                    <Edit className="h-4 w-4" />
                  </Button>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleDuplicate(template)}
                    className="bg-transparent border-white/20 text-white hover:bg-white/10 hover:text-white"
                  >
                    <Copy className="h-4 w-4" />
                  </Button>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleDelete(template)}
                    className="bg-transparent border-red-500/30 text-red-400 hover:bg-red-500/20 hover:text-red-300"
                  >
                    <Trash2 className="h-4 w-4" />
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}

      {/* Edit Dialog */}
      <Dialog open={isEditDialogOpen} onOpenChange={setIsEditDialogOpen}>
        <DialogContent className="max-w-6xl max-h-[90vh] overflow-y-auto bg-[#0e1340] border border-white/10 text-white">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2 text-white">
              <Bot className="h-5 w-5 text-indigo-400" />
              Edit Template: {selectedTemplate?.name}
            </DialogTitle>
          </DialogHeader>
          {selectedTemplate && (
            <DefaultAssistantTemplateForm
              template={selectedTemplate}
              isEditing={true}
              onSuccess={() => {
                setIsEditDialogOpen(false);
                setSelectedTemplate(null);
                queryClient.invalidateQueries({ queryKey: ['defaultAssistantTemplates'] });
                queryClient.invalidateQueries({ queryKey: ['templateCategories'] });
              }}
              onCancel={() => {
                setIsEditDialogOpen(false);
                setSelectedTemplate(null);
              }}
            />
          )}
        </DialogContent>
      </Dialog>

      {/* Preview Dialog */}
      <TemplatePreviewDialog
        template={selectedTemplate}
        open={isPreviewDialogOpen}
        onOpenChange={setIsPreviewDialogOpen}
      />
    </div>
  );
};

export default DefaultAssistants;
